import request from '@/plugin/axios';
export default {
  name: 'task-manager',
  data: function data() {
    return {
      filename: __filename,
      loading: true,
      query: {
        title: '',
        finish: null
      },
      create: {
        id: '',
        title: '',
        content: '',
        userId: '',
        userName: '',
        endTime: '',
        startTime: ''
      },
